<template>
  <strong>
    {{ formatted }}
  </strong>
</template>

<script>

import { mapGetters } from "vuex";

export default {
  name: "TicketTotal",
  components: {
  },
  props: {
    count: {
      type: Number,
      required: true
    },
    price: {
      type: Number,
      required: true
    },
  },
  computed: {
    total() {
      return this.count * this.price
    },
    formatted() {
      return `$${this.total / 100}`
    }
  },
  methods: {
  }
};
</script>

